* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Exo;
}

.main {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-color: #0c5db9; */
}
.profile-card {
  position: relative;
  font-family: sans-serif;
  width: 300px;
  /* height: 300px; */
  background: #fff;
  padding: 30px;
  /* border-radius: 50%; */
  box-shadow: 0 0 22px #3336;
  transition: 0.6s;
  margin: 0 25px;
  border-radius: 10px;
  height: 400px;
}
